body{
    font-family: Montserrat,sans-serif;

}

h1{
    font-weight: 900;
}
/* Navbar */
.fixed-top{
    background-color:#fff ;
}

.navbar-light .navbar-nav .nav-link{
    text-decoration: none;
    font-weight: 600;
    color:black;
    font-size: 1.2rem;
    transition: .6s;
    width:40%;
    
}

.navbar-light .navbar-brand{
    color: #F9004D !important;
    font-size: 2.2rem;
    font-weight: 700;
    text-decoration: none;
    
}
.navbar-light .navbar-brand:hover{
    animation: nav-brand-rotate 1.5s linear infinite;
}
@keyframes nav-brand-rotate{
    to{
        transform: rotate(360deg);
    }
}
.navbar-light .navbar-nav .nav-link:hover{
    color:#F9004D;
    
}
.active-class{
    color: #F9004D;
    border-bottom: 2px solid #F9004D;
}
.nav-bar-scroll{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
/* Navbar */


/* Home starts */
.wrapper {
    display: inline-flex;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: center;
    height:150px;
  }
  
  i {
    padding: 0px 10px;
  }
  
  .wrapper  a:nth-child(1) i {
    color: #4867AA;
    cursor: pointer;
    text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
    transition: all ease-in-out 150ms;
  }
  
  .wrapper  a:nth-child(1) i:hover  {
    margin-top: -10px;
    text-shadow: 0px 16px 10px rgba(0, 0, 0, 0.3);
    transform: translate(0, -8);
  }
  
  .wrapper a:nth-child(2) i{
    color: #1DA1F2;
    cursor: pointer;
    text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
    transition: all ease-in-out 150ms;
  }
  
  .wrapper a:nth-child(2) i:hover {
    margin-top: -10px;
    text-shadow: 0px 16px 10px rgba(0, 0, 0, 0.3);
    transform: translate(0, -8);
  }
  
  .wrapper a:nth-child(3) i {
    color: #444;
    cursor: pointer;
    text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
    transition: all ease-in-out 150ms;
  }
  
  .wrapper a:nth-child(3) i:hover {
    margin-top: -10px;
    text-shadow: 0px 14px 10px rgba(0, 0, 0, 0.4);
    transform: translate(0, -5);
  }
  .wrapper a:nth-child(4) i {
    color: #0C61BF;
    cursor: pointer;
    text-shadow: 0px 7px 10px rgba(0, 0, 0, 0.4);
    transition: all ease-in-out 150ms;
  }
  .wrapper a:nth-child(4) i:hover {
    margin-top: -10px;
    text-shadow: 0px 16px 10px rgba(0, 0, 0, 0.3);
    transform: translate(0, -8);
  }

/* Home starts */



