/* Portfolio starts here */
/* Portfolio starts here */
.port-item{
    width:300px;
    height: 400px;
   margin: 0 auto;
   border-radius: 10px;
   position: relative;
   transition:.5s ease-in-out;
   box-shadow: rgba(0,0,0,.22) 0px 15px 12px;
}
.port-item img{
    width:100%;
    height:100%;


}

#text{
    display: none;
}

.port-item:hover #text{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 50px;
    color: white;
    transform: translate(-50% , -50%);
    -ms-transform: translate(-50%,-50%);
    background-color: #F9004D;
    opacity: .85;
    width: 100%;
    height: 100%;
    border-radius: 10px;

 
  }
  .view-more-btn{
      text-transform: uppercase;
      background-color: #F9004D;
      color: #fff;
      font-weight: bold;
      transition: .4s;
     
  }
  .view-more-btn:hover{
      background-color: #fff;
      color: #F9004D;
      border: 1px solid #F9004D;
      
  }
  .button-wrapper{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .button-wrapper .btn{
    display: block;
    width: 200px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    font-family: sans-serif;
    text-decoration: none;
    color: #333;
    border: 2px solid #333;
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    transition: all .35s;
  }
  
  .button-wrapper .btn span{
    position: relative;
    z-index: 2;
  }
  
  .button-wrapper .btn:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #ff003b;
    transition: all .35s;
  }
  
  .button-wrapper .btn:hover{
    color: #fff;
  }
  
  .button-wrapper .btn:hover:after{
    width: 100%;
  }
  
/* Portfolio ends here */
